
.example-root {
  margin-top: 60px;
  margin-bottom: 120px;
  width: 100%;
  overflow: hidden;
  position: relative;
  height: 570px;
}
.example-root .arrow-l {
  position: absolute;
  left: 0px;
  top: -50%;
  transform: translate(0, 50%);
  z-index: 999;
}
.example-root .arrow-l img {
  width: 180px;
  height: 570px;
}
.example-root .arrow-r {
  position: absolute;
  right: 0px;
  z-index: 999;
}
.example-root .arrow-r img {
  width: 180px;
  height: 570px;
}
.example-root .container {
  position: absolute;
  left: 0px;
  top: 0px;
  transition-property: left;
  transition-duration: .5s;
  transition-timing-function: ease-in;
  transition-delay: .1s;
}
.example-root .container .img-item {
  width: 350px;
  height: 520px;
  display: inline-block;
  position: relative;
  background-color: #f5f7fa;
}
.example-root .container .img-item img {
  width: 310px;
  height: 520px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}



