
.root {
  display: flex;
  justify-content: center;
  background: #3B3E4D;
}
.bottom {
  display: flex;
  flex-direction: row;
  height: 300px;
  box-sizing: border-box;
  padding-top: 60px;
  padding-bottom: 90px;
}
.bottom .left {
  width: 267px;
  border-right: 1px solid #616575;
  display: flex;
  flex-direction: column;
}
.bottom .left img {
  width: 80px;
  height: 100px;
}
.bottom .left .cml-logo-text {
  font-family: PingFangSC-Regular;
  font-size: 12px;
  color: #7A8899;
  margin-top: 4px;
  padding: 0;
}
.bottom .right {
  flex: 1;
  display: flex;
}
.bottom .right .right-item {
  padding-left: 56px;
}
.bottom .right .right-item .right-title {
  font-family: PingFangSC-Regular;
  font-size: 12px;
  color: #7A8899;
  padding-bottom: 9px;
}
.bottom .right .right-item .right-subtitle {
  position: relative;
  font-family: PingFangSC-Regular;
  font-size: 14px;
  color: #AFB7C2;
  padding-top: 10px;
  cursor: pointer;
}
.bottom .right .right-item .right-subtitle a {
  color: #fff;
  cursor: pointer;
}
.bottom .right .right-item .right-subtitle img {
  display: none;
  position: absolute;
  top: 20px;
  left: 30px;
  z-index: 999;
}
.bottom .right .right-item .right-subtitle:hover img {
  width: 120px;
  height: 120px;
  display: inline-block;
}



